




import Vue from "vue";

export default Vue.extend({
    props: {
        placeholder: {
            type: String,
            required: true,
        },
    },
    computed: {
        inputClasses() {
            return {
                "w-full": true,
                "py-2": true,
                "border-b-2": true,
                "border-transparent": true,
                "outline-none": true,
                "focus:border-black": true,
            };
        },
    },
    methods: {
        updateValue(value: string) {
            this.$emit("input", value);
        },
    },
});
