








import Vue from "vue";

export default Vue.extend({
    props: {
        image: {
            type: String,
            required: true,
        },
        placeholder: {
            type: String,
            required: true,
        },
        type: {
            type: String,
            required: true,
            validator: (value) =>
                ["movie", "series", "game"].includes(value),
        },
    },
    computed: {
        isSeries() {
            return this.type === "series";
        },
        isMovie() {
            return this.type === "movie";
        },
        isGame() {
            return this.type === "game";
        },
        cardTypeClasses() {
            return {
                "px-4": true,
                "py-2": true,
                "uppercase": true,
                "tracking-wider": true,
                "text-white": true,
                "bg-blue-500": this.isSeries,
                "bg-red-500": this.isMovie,
                "bg-yellow-500": this.isGame,
            };
        },
    },
});
